ar(--accent);\n}\n\nbutton, a.button {\n  padding: 1vmin;\n  color: var(--black);\n  background-color: var(--white);\n  font-size: 2vmin;\n  font-weight: 700;\n  text-decoration: none;\n  text-align: center;\n  border: .1vmin solid var(--white);\n  outline: none;\n  cursor: pointer;\n}\n\nbutton:hover {\n  color: var(--accent);\n  border: .1vmin solid var(--accent);\n}\n\nbutton.btn-sm {\n  font-size: 1.5vmin;\n  padding: .6vmin .8vmin;\n}\n\nbutton.btn-xs {\n  font-size: 1vmin;\n  padding: .4vmin .5vmin;\n}\n\nbutton:disabled, form:invalid button[type=\"submit\"] {\n  cursor: not-allowed;\n  color: rgba(0, 0, 0, 0.5)\n} \n\nbutton[type=\"submit\"] {\n  grid-column: span 2;\n  margin: 1vmin 0 0;\n  color: var(--accent)\n}"],"sourceRoot":""}]);
